div {
    height: 268px;
    width: 1035px;
    margin: 0 auto;
    padding: 0;
}

.banner {
    background-image: url(../assets/images/sky_03.png);
    background-repeat: no-repeat;
}

.cloud {
    background-image: url(../assets/images/cloud_03.png);
    background-repeat: repeat-x;

}

.horizont {
    background-image: url(../assets/images/horizont_03.png);
    background-repeat: repeat-x;
}

.hill {
    background-image: url(../assets/images/hill_03.png);
    background-repeat: repeat-x;
    position: relative;
}

.glass,
.glass2 {
    background-image: url(../assets/images/grass_03.png);
    background-repeat: repeat-x;
    position: absolute;
}

.auto {
    /*    border: thin #000 solid;*/
    position: absolute;
    width: 234px;
    height: 137px;
    bottom: 0;
    left: 400px;
}

.trailer {
    background-image: url(../assets/images/trailer_03.png);
    background-repeat: no-repeat;
    width: 234px;
    height: 114px;

    -webkit-transform-origin: center bottom;

    transform-origin: center bottom;
}

.wheel {
    background-image: url(../assets/images/wheel_03.png);
    background-repeat: no-repeat;
    width: 63px;
    height: 63px;
    position: absolute;
    bottom: 0;
}

#anterior {
    left: 17px;
}

#rear {
    left: 146px;
}
#rear2 {
    left: 196px;
}

/*----------Animation-start-----------*/

.cloud {
    -webkit-animation: landscape 120s infinite linear;
    animation: landscape 120s infinite linear;
}

.horizont {

    -webkit-animation: landscape 35s infinite linear;
    animation: landscape 35s infinite linear;
}

.hill {
    -webkit-animation: landscape 20s infinite linear;
    animation: landscape 20s infinite linear;
}

.glass {
    -webkit-animation: landscape 10s infinite linear;
    animation: landscape 10s infinite linear;
}

.glass2 {
    -webkit-animation: landscape 5s infinite linear;
    animation: landscape 5s infinite linear;
}

.wheel {
    -webkit-animation: wheel 1s infinite reverse linear;
    animation: wheel 1s infinite reverse linear;
}

.trailer {
    -webkit-animation: trailer 0.5s infinite linear alternate;
    animation: trailer 0.5s infinite linear alternate;

}
/*----------Animation-end-----------*/
/*-----------keyframes-start---------*/
@-webkit-keyframes landscape {
    from {
        background-position: -1035px 0;
    }

    to {
        background-position: 1035px 0;
    }
}

@keyframes landscape {
    from {
        background-position: -1035px 0;
    }

    to {
        background-position: 1035px 0;
    }
}

@keyframes wheel {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes trailer {
    from {
        -webkit-transform: scaleY(0.95);
        transform: scaleY(0.95);
    }

    to {
        -webkit-transform: scaleY(0.95);
        transform: scaleY(1);
    }
}

@keyframes trailer {
    from {
        -webkit-transform: scaleY(0.95);
        transform: scaleY(0.95);
    }

    to {
        -webkit-transform: scaleY(0.95);
        transform: scaleY(1);
    }
}
/*-----------keyframes-end---------*/

